.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #282a36;
}

.hljs-keyword,
.hljs-selector-tag,
.hljs-literal,
.hljs-section,
.hljs-link {
  color: #8be9fd;
}

.hljs-function .hljs-keyword {
  color: #ff79c6;
}

.hljs,
.hljs-subst {
  color: #f8f8f2;
}

.hljs-string,
.hljs-title,
.hljs-name,
.hljs-type,
.hljs-attribute,
.hljs-symbol,
.hljs-bullet,
.hljs-addition,
.hljs-variable,
.hljs-template-tag,
.hljs-template-variable {
  color: #f1fa8c;
}

.hljs-comment,
.hljs-quote,
.hljs-deletion,
.hljs-meta {
  color: #6272a4;
}

.hljs-keyword,
.hljs-selector-tag,
.hljs-literal,
.hljs-title,
.hljs-section,
.hljs-doctag,
.hljs-type,
.hljs-name,
.hljs-strong {
  font-weight: bold;
}

.hljs-emphasis {
  font-style: italic;
}

/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  background: #ffff00;
  font-family: "Overpass";
  font-size: 3.5vmin; }

h1, h2, h3, h4, h5 {
  font-family: "Overpass Mono"; }

h2, h3, h4, h5 {
  margin-top: 2em; }
  h2 a:after, h3 a:after, h4 a:after, h5 a:after {
    content: '\21e2'; }

ul, ol {
  padding: 0; }
  ul li, ol li {
    margin-left: 1em; }

ul li {
  list-style: square; }

a {
  color: #007fff;
  text-decoration: none;
  font-weight: bold; }
  a:hover {
    color: #000; }
  a:visited {
    color: #007fff; }
  a:visited:hover {
    color: #000; }
  a code {
    color: #007fff; }

pre, code {
  font-family: "Overpass Mono"; }

code {
  color: #f0f; }

pre > code {
  color: #000;
  padding: 0.5em; }

blockquote {
  color: #ff0;
  background: #000;
  padding: 1em;
  margin: 0; }
  blockquote p:first-child {
    margin-top: 0; }
  blockquote p:last-child {
    margin-bottom: 0; }
  blockquote cite {
    font-style: normal; }

table {
  font-size: 80%; }
  table tr th, table tr td {
    padding: 0.5em;
    background: #000;
    color: #ff0; }

hr {
  border: none;
  background: transparent;
  height: 1;
  border-top: 5px dotted #f0f;
  margin-top: 2em;
  margin-bottom: 2em; }

img, iframe, video {
  width: 100%;
  height: auto; }

img {
  border: none; }

iframe, video {
  min-height: 50vh; }

#header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 16vmin; }
  #header h1.main-title {
    position: absolute;
    right: 5vw;
    top: 0;
    margin: 0;
    line-height: 16vmin; }
    #header h1.main-title a {
      font-weight: normal;
      display: inline-block; }
    #header h1.main-title .back {
      display: inline-block;
      transform: rotateX(90deg);
      transition: transform 300ms 1000ms linear; }
    .interior-page #header h1.main-title .back {
      display: inline-block;
      transform: rotateX(0deg); }
  #header .main-nav {
    position: absolute;
    top: 0;
    left: 5vw;
    width: 16vmin; }
    #header .main-nav a.main-nav-icon {
      font-family: "Overpass";
      font-size: 16vmin;
      line-height: 16vmin;
      font-weight: normal;
      color: #007fff;
      transform: rotateX(0deg);
      transition: transform 200ms linear;
      display: block; }
      #header .main-nav a.main-nav-icon:hover {
        color: #000; }
    #header .main-nav ol.main-nav-items {
      max-height: 0;
      overflow: hidden;
      margin: 0;
      padding: 0;
      width: 88vw;
      transition: max-height 200ms linear; }
      #header .main-nav ol.main-nav-items li {
        margin: 0;
        margin-bottom: 0.5em;
        list-style: none;
        transform: rotateX(90deg);
        transition: transform 200ms linear; }
        #header .main-nav ol.main-nav-items li a {
          padding: 1vmin 3vmin;
          background: white;
          border: 5px solid #fff;
          color: #f0f;
          display: block;
          font-size: 4vh; }
          #header .main-nav ol.main-nav-items li a:hover {
            border-color: #f0f;
            color: #007fff; }
    #header .main-nav.open a.main-nav-icon {
      transform: rotateX(180deg); }
    #header .main-nav.open ol.main-nav-items {
      max-height: 300vh; }
      #header .main-nav.open ol.main-nav-items li {
        transform: rotateX(0deg); }

#main {
  padding: 16vmin 5vw; }
  #main h1 {
    font-size: 225%;
    color: #f0f;
    margin-top: 48vmin; }
  #main h1:first-of-type {
    margin-top: 0; }
  #main h2 {
    font-size: 150%; }
  #main h3 {
    font-size: 125%; }
  #main h4 {
    font-size: 100%; }
  #main section.full-frame {
    height: 100vh;
    padding: 5vmin; }
  #main .code-columns {
    display: flex;
    align-items: stretch;
    justify-content: space-between; }
    #main .code-columns p {
      display: none; }
    #main .code-columns pre {
      width: 49%;
      font-size: 50%; }
      #main .code-columns pre code {
        height: 100%; }
